
"use client";

import { useEffect, useState } from 'react';
import Link from 'next/link';
import { useAuth } from '@/context/AuthContext';
import ProtectedRoute from '@/components/ProtectedRoute';
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Skeleton } from "@/components/ui/skeleton";
import { Briefcase, Users, Calendar, Edit, Trash2, Plus } from "lucide-react";

export default function MyOpportunitiesPage() {
    const { user } = useAuth();
    const [opportunities, setOpportunities] = useState<any[]>([]);
    const [loading, setLoading] = useState(true);

    const fetchOpportunities = async () => {
        if (!user) return;
        try {
            const token = await user.getIdToken();
            const response = await fetch(`${process.env.NEXT_PUBLIC_API_URL || 'http://localhost:5000/api'}/opportunities/list?facultyId=${user.uid}`, {
                headers: { 'Authorization': `Bearer ${token}` }
            });
            if (!response.ok) throw new Error('Failed to fetch opportunities');
            const data = await response.json();
            setOpportunities(data);
        } catch (error) {
            console.error(error);
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        fetchOpportunities();
    }, [user]);

    const getStatusColor = (status: string) => {
        return status === 'Open'
            ? 'bg-green-100 text-green-800 dark:bg-green-900/20 dark:text-green-400'
            : 'bg-gray-100 text-gray-800 dark:bg-gray-700 dark:text-gray-300';
    };

    const isDeadlinePassed = (deadline: any) => {
        if (!deadline?._seconds) return false;
        return new Date(deadline._seconds * 1000) < new Date();
    };

    return (
        <ProtectedRoute allowedRoles={['faculty', 'admin']}>
            <div className="max-w-7xl mx-auto py-10 space-y-8 animate-in fade-in-50">
                <div className="flex justify-between items-center">
                    <div>
                        <h1 className="text-3xl font-bold tracking-tight">My Opportunities</h1>
                        <p className="text-muted-foreground">Manage opportunities you've posted.</p>
                    </div>
                    <Link href="/faculty/opportunities">
                        <Button>
                            <Plus className="w-4 h-4 mr-2" />
                            Post New Opportunity
                        </Button>
                    </Link>
                </div>

                <Card>
                    <CardHeader>
                        <CardTitle>Posted Opportunities ({opportunities.length})</CardTitle>
                    </CardHeader>
                    <CardContent>
                        {loading ? (
                            <div className="space-y-2">
                                {[1, 2, 3].map(i => <Skeleton key={i} className="h-20 w-full" />)}
                            </div>
                        ) : opportunities.length === 0 ? (
                            <div className="text-center py-12 text-muted-foreground">
                                <Briefcase className="w-12 h-12 mx-auto mb-4 opacity-50" />
                                <p className="mb-4">No opportunities posted yet.</p>
                                <Link href="/faculty/opportunities">
                                    <Button>Post Your First Opportunity</Button>
                                </Link>
                            </div>
                        ) : (
                            <Table>
                                <TableHeader>
                                    <TableRow>
                                        <TableHead>Title</TableHead>
                                        <TableHead>Status</TableHead>
                                        <TableHead>Applicants</TableHead>
                                        <TableHead>Deadline</TableHead>
                                        <TableHead>Posted</TableHead>
                                        <TableHead className="text-right">Actions</TableHead>
                                    </TableRow>
                                </TableHeader>
                                <TableBody>
                                    {opportunities.map((opp) => (
                                        <TableRow key={opp.opportunityId}>
                                            <TableCell>
                                                <div className="flex flex-col">
                                                    <span className="font-medium">{opp.title}</span>
                                                    <span className="text-xs text-muted-foreground line-clamp-1">
                                                        {opp.description}
                                                    </span>
                                                </div>
                                            </TableCell>
                                            <TableCell>
                                                <Badge className={getStatusColor(opp.status)}>
                                                    {opp.status}
                                                </Badge>
                                            </TableCell>
                                            <TableCell>
                                                <div className="flex items-center gap-1">
                                                    <Users className="w-3 h-3" />
                                                    <span className="text-sm font-medium">{opp.applicantCount || 0}</span>
                                                </div>
                                            </TableCell>
                                            <TableCell>
                                                <div className="flex flex-col">
                                                    <span className={`text-sm ${isDeadlinePassed(opp.applicationDeadline) ? 'text-destructive' : ''}`}>
                                                        {opp.applicationDeadline?._seconds
                                                            ? new Date(opp.applicationDeadline._seconds * 1000).toLocaleDateString()
                                                            : 'N/A'}
                                                    </span>
                                                    {isDeadlinePassed(opp.applicationDeadline) && (
                                                        <span className="text-xs text-destructive">Expired</span>
                                                    )}
                                                </div>
                                            </TableCell>
                                            <TableCell className="text-sm text-muted-foreground">
                                                {opp.createdAt?._seconds
                                                    ? new Date(opp.createdAt._seconds * 1000).toLocaleDateString()
                                                    : 'N/A'}
                                            </TableCell>
                                            <TableCell className="text-right">
                                                <div className="flex justify-end gap-2">
                                                    <Link href="/faculty/applications">
                                                        <Button
                                                            variant="ghost"
                                                            size="sm"
                                                            title="View Applications"
                                                        >
                                                            <Users className="w-4 h-4 mr-1" />
                                                            View Apps
                                                        </Button>
                                                    </Link>
                                                </div>
                                            </TableCell>
                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                        )}
                    </CardContent>
                </Card>
            </div>
        </ProtectedRoute>
    );
}
